.overlay-image__container{
  position: relative;
}

.popup__image-container{
  position: relative;
	height: 75vh;
	width: 75vw;
}

.popup {
  position: fixed;
  font-family: 'Inter', Arial, sans-serif;
  font-size: 24px;
  font-weight: 900;
  font-style: normal;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  overflow: hidden;
  visibility: hidden;
}

.popup_opened {
	display: flex;
	justify-content: center;
	align-items: center;
  visibility: visible;

}

.popup_content {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: #fff;
	border-radius: 10px;
	padding: 20px;
	width: 100%;
	max-width: 430px;
	height: 100%;
	max-height: 330px;
}

.popup__formDeleteCard {
	background-color: red;
}
.popup-animation {
	animation: zoomIn 0.5s ease-in-out;
}
.popup-cierre {
	transition: opacity 1s ease;
	opacity: 0;
	-webkit-transition: opacity 1s ease;
	-moz-transition: opacity 1s ease;
	-ms-transition: opacity 1s ease;
	-o-transition: opacity 1s ease;
}

@keyframes zoomIn {
	0% {
		transform: scale(0);
	}
	100% {
		transform: scale(1);
	}
}

.invisible {
	display: none;
}

@media (min-width: 768px) {
	.popup__form {
		max-width: 430px;
	}
}
.popup__image {
	width: 75vw;
	object-fit:contain;
}
.popup_closing {
	animation: fadeout 0.3s ease-in-out forwards;
	-webkit-animation: fadeout 0.3s ease-in-out forwards;
}
