.popup__input {
	font-size: 14px;
	font-style: normal;
	font-weight: 400;
	display: block;
	width: 100%;
	height: 27px;
	border: none;
	border-bottom: 1px solid rgba(0, 0, 0, 0.2);
	padding: 10px;
	margin-bottom: 15px;
}
.popup__input:focus {
	outline: none;
}
.popup__input:invalid {
	border-bottom: 1px solid red;
}
.popup__input:valid {
	border-bottom: 1px solid green;
}

.popup__input::placeholder {
	font-size: 14px;
	font-style: normal;
	font-weight: 400;
	color: #c4c4c4;
}
.form-input-error {
	width: auto;
	height: 16px;
	color: rgb(255, 0, 0);
	font-family: Inter;
	font-size: 12px;
	font-style: normal;
	font-weight: 400;
	line-height: normal;
	margin-bottom: 20px;
}
.popup__input:placeholder-shown {
	padding: 0;
}
